import { CurrencyAmount, ETHER, Percent, Price } from "@pancakeswap-libs/sdk"
import { OpenOrder, Order } from "entities/Order"
import invariant from 'tiny-invariant'
import getCurrencyAddress from "utils/getCurrencyAddress"

/**
 * Options for producing the arguments to send call to the router.
 */
 export interface TradeOptions {
  /**
   * How much the execution price is allowed to move unfavorably from the trade execution price.
   */
  allowedSlippage: Percent
  /**
   * How long the swap is valid until it expires, in seconds.
   * This will be used to produce a `deadline` parameter which is computed from when the swap call parameters
   * are generated.
   */
  ttl: number
  /**
   * The account that should receive the output of the swap.
   */
  recipient: string

  /**
   * Whether any of the tokens in the path are fee on transfer tokens, which should be handled with special methods
   */
  feeOnTransfer?: boolean
}

/**
 * The parameters to use in the call to the Uniswap V2 Router to execute a trade.
 */
export interface OrderBookCallParameters {
  /**
   * The method to call on the Uniswap V2 Router.
   */
  methodName: string
  /**
   * The arguments to pass to the method, all hex encoded.
   */
  args: (string | string[])[]
  /**
   * The amount of wei to send in hex.
   */
  value: string
}

function toHex(amount: CurrencyAmount | Price): string {
  return `0x${amount.raw.toString(16)}`
}

const ZERO_HEX = '0x0'

/**
 * Represents the OrderBook, and has static methods for helping execute trades.
 */
export const OrderBookContract = {
  /**
   * Produces the on-chain method name to call and the hex encoded parameters to pass as arguments for a given trade.
   * @param order to produce call parameters for
   * @param options options for the call parameters
   */
  openOrderCallParameters(order: Order, options: TradeOptions): OrderBookCallParameters {
    const sendBNB = order.baseCurrency === ETHER
    const baseTokenAddr = getCurrencyAddress(order.baseCurrency)
    const quoteTokenAddr = getCurrencyAddress(order.quoteCurrency)

    invariant(baseTokenAddr && quoteTokenAddr, 'Base and Quote tokens required')
    invariant(baseTokenAddr !== quoteTokenAddr, 'Token In = Token Out')
    invariant(options.ttl > 0, 'TTL')

    const amount = toHex(order.amount)
    const price = `0x${Number(order.price.toExact()).toString(16)}`
    // const deadline = `0x${(Math.floor(new Date().getTime() / 1000) + options.ttl).toString(16)}`

    return {
      methodName: 'openOrder',
      args: [baseTokenAddr, quoteTokenAddr, price, amount],
      value: sendBNB ? amount : ZERO_HEX
    }
  },

  cancelOrderCallParameters(order: OpenOrder, { ttl }: { ttl: number }): OrderBookCallParameters {
    const baseTokenAddr = getCurrencyAddress(order.baseCurrency)
    const quoteTokenAddr = getCurrencyAddress(order.quoteCurrency)

    invariant(baseTokenAddr && quoteTokenAddr, 'Base and Quote tokens required')
    
    return {
      methodName: 'cancelOrder',
      args: [baseTokenAddr, quoteTokenAddr, order.id.toHexString()],
      value: ZERO_HEX
    }
  },

  executeOrderCallParameters(order: OpenOrder, amount: CurrencyAmount, { ttl }: { ttl: number }): OrderBookCallParameters {
    const sendBNB = order.quoteCurrency === ETHER
    const baseTokenAddr = getCurrencyAddress(order.baseCurrency)
    const quoteTokenAddr = getCurrencyAddress(order.quoteCurrency)

    invariant(baseTokenAddr && quoteTokenAddr, 'Base and Quote tokens required')

    const hexAmount = toHex(amount)
    
    return {
      methodName: 'executeOrder',
      args: [baseTokenAddr, quoteTokenAddr, order.id.toHexString(), hexAmount],
      value: sendBNB ? hexAmount : ZERO_HEX
    }
  },
}
