import { BigintIsh, Currency, CurrencyAmount } from "@pancakeswap-libs/sdk";
import { BigNumber } from "ethers";

export type Order = {
  amount: CurrencyAmount
  price: CurrencyAmount
  baseCurrency: Currency
  quoteCurrency: Currency
}

export type OpenOrder = {
  id: BigNumber
  amount: number
  price: number
  maker: string
  baseCurrency: Currency
  quoteCurrency: Currency
}

// Like CurrencyAmount but with a public constructor
export class OpenCurrencyAmount extends CurrencyAmount {
  // eslint-disable-next-line no-useless-constructor
  constructor(currency: Currency, amount: BigintIsh) {
    super(currency, amount)
  }
}