import AppBody from 'pages/AppBody'
import React, { useCallback, useState } from 'react'
import { Alert, Button, OverlayTrigger, Spinner, Table, Tooltip } from 'react-bootstrap'
import { useModal } from '@pancakeswap-libs/uikit'
import { Currency } from '@pancakeswap-libs/sdk'
import PageHeader from 'components/PageHeader'
import useOrderList from 'hooks/orders'
import { useActiveWeb3React } from 'hooks'
import { OpenOrder } from 'entities/Order'
import styled from 'styled-components'
import { useCancelOrderCallback } from 'hooks/useCancelOrderCallback'
import { useUserDeadline } from 'state/user/hooks'
import { AlertCircle } from 'react-feather'
import useInterval from 'hooks/useInterval'
import ExecuteOrderModal from 'components/ExecuteOrderModal'
import exactMath from 'exact-math'
import ConnectWalletButton from 'components/ConnectWalletButton'

interface Props {
  baseCurrency?: Currency
  quoteCurrency?: Currency
}

// const SmallButton = styled(Button)`
//   height: 32px;
// `

let lastExecTime = 0

export default function OrderBook ({ baseCurrency, quoteCurrency }: Props) {
  const { orderList, loading, refresh: refreshOrderList, error } = useOrderList(baseCurrency, quoteCurrency)
  const { account } = useActiveWeb3React()
  const [orderLoading, setOrderLoading] = useState<{ [key: number]: boolean | string }>({})
  const [orderError, setOrderError] = useState<{ [key: number]: string | null }>({})
  const [cancelOrder, setCancelOrder] = useState<OpenOrder | null>(null)

  // Execute order modal
  let executeOrder: OpenOrder | null = null
  const getExecuteOrder = () => executeOrder
  const [onExecuteClick] = useModal(<ExecuteOrderModal getOrder={getExecuteOrder} onDismiss={() => undefined} />)

  useInterval(refreshOrderList, 1000)

  const [deadline] = useUserDeadline()

  const { callback: cancelOrderCallback, error: swapCallbackError } = useCancelOrderCallback(
    cancelOrder,
    deadline
  )

  const handleCancelClick = (e: React.MouseEvent, order: OpenOrder) => {
    e.preventDefault()
    setCancelOrder(order)
    setOrderLoading({
      ...orderLoading,
      [order.id.toNumber()]: true
    })
    setOrderError({
      ...orderError,
      [order.id.toNumber()]: null
    })
  }

  const doCancelOrder = useCallback(async (order: OpenOrder) => {
    if (!cancelOrderCallback) {
      return
    }
    setCancelOrder(null)

    if (Date.now() - lastExecTime < 10) {
      return
    }
    lastExecTime = Date.now()

    try {
      const txHash = await cancelOrderCallback()
      setOrderError({
        ...orderError,
        [order.id.toNumber()]: null
      })
      setOrderLoading({
        ...orderLoading,
        [order.id.toNumber()]: txHash
      })
    } catch (err) {
      console.error(err)
      setOrderError({
        ...orderError,
        [order.id.toNumber()]: err.message
      })
      setOrderLoading({
        ...orderLoading,
        [order.id.toNumber()]: false
      })
    }
  }, [cancelOrderCallback, orderLoading, orderError])

  const handleExecuteClick = (e: React.MouseEvent, order: OpenOrder) => {
    e.preventDefault()
    executeOrder = order
    onExecuteClick()
  }

  if (cancelOrder) {
    doCancelOrder(cancelOrder)
  }

  if (loading) {
    return <Spinner animation="border" variant="primary" />
  }
  if (error) {
    return <Alert variant="danger">{ error?.message }</Alert>
  }

  return (
    <div style={{ marginBottom: 32 }}>
      <AppBody>
        <PageHeader
          title={`Order Book ${baseCurrency?.symbol}/${quoteCurrency?.symbol}`}
          description={`Buy ${baseCurrency?.symbol} / Sell ${quoteCurrency?.symbol}`}
          hideSettings
          hideTransactions
        />

        <Table hover variant="dark">
          <thead>
            <tr>
              <th>Price ({ quoteCurrency?.symbol })</th>
              <th>Amount ({ baseCurrency?.symbol })</th>
              <th>Total ({ quoteCurrency?.symbol })</th>
              <th> </th>
            </tr>
          </thead>
          <tbody>
            {
              orderList.map(order => (
                <tr key={order.id.toNumber()}>
                  <td>{ order.price }</td>
                  <td>{ order.amount }</td>
                  <td>{ exactMath.mul(order.price, order.amount) }</td>
                  <td style={{ textAlign: 'center' }}>
                    {
                      orderLoading[order.id.toNumber()]
                        ? <Spinner animation="border" variant="primary" />
                        : order.maker === account
                          ? <Button onClick={e => handleCancelClick(e, order)} size="sm">Cancel</Button>
                          : <Button onClick={e => handleExecuteClick(e, order)} size="sm">Execute</Button>
                    }
                    {
                      orderError[order.id.toNumber()] && (
                        <OverlayTrigger
                          placement="right"
                          overlay={
                            <Tooltip id={`${order.id.toNumber()}-error-tooltip`}>
                              { orderError[order.id.toNumber()] }
                            </Tooltip>
                          }
                        >
                          <AlertCircle style={{ color: 'red', marginTop: 8 }} />
                        </OverlayTrigger>
                      )
                    }
                  </td>
                </tr>
              ))
            }
          </tbody>
        </Table>

        {
          !account
            ? <div style={{ margin: 16 }}><ConnectWalletButton block /></div>
            : !orderList.length && <p style={{ margin: '8px 0 32px 8px' }}>There are no open orders at this time.</p>
        }
      </AppBody>
    </div>
  )
}
