import React, { useEffect, useState } from 'react'
import { Box, Modal } from '@pancakeswap-libs/uikit'
import { Button, Spinner } from 'react-bootstrap'
import exactMath from 'exact-math'
import { OpenOrder } from 'entities/Order'
import CurrencyInputPanel from 'components/CurrencyInputPanel'
import { BottomGrouping } from 'components/swap/styleds'
import { ApprovalState, useApproveOrderCallback } from 'hooks/useApproveCallback'
import { AutoRow, RowBetween } from 'components/Row'
import Loader from 'components/Loader'
import { tryParseAmount } from 'state/swap/hooks'
import { useExecuteOrderCallback } from 'hooks/useExecuteOrderCallback'
import { useUserDeadline } from 'state/user/hooks'

interface ExecuteOrderModalProps {
  getOrder: () => OpenOrder | null
  onDismiss: () => void
  // onCurrencySelect: (currency: Currency) => void
}

export default function ExecuteOrderModal({
  getOrder,
  onDismiss,
}: ExecuteOrderModalProps) {
  const order = getOrder()
  const orderMaxAmount: number = order ? exactMath.mul(order.price, order.amount) : 0
  const [amount, setAmount] = useState<string>(orderMaxAmount.toString())
  const currencyAmount = tryParseAmount(amount, order?.quoteCurrency)
  const [approval, approveCallback] = useApproveOrderCallback(currencyAmount)
  const [approvalSubmitted, setApprovalSubmitted] = useState<boolean>(false)
  const [orderError, setOrderError] = useState<string | null>(null)
  const [orderLoading, setOrderLoading] = useState<boolean>(false)
  const receiveAmount = order
    ? tryParseAmount(exactMath.div(Number(amount), order.price).toString(), order?.baseCurrency)
    : null

  const [deadline] = useUserDeadline()
  const { callback: executeOrderCallback, error: executeCallbackError } = useExecuteOrderCallback(
    order,
    currencyAmount ?? null,
    deadline
  )

  // mark when a user has submitted an approval, reset onTokenSelection for input field
  useEffect(() => {
    if (approval === ApprovalState.PENDING) {
      setApprovalSubmitted(true)
    }
  }, [approval, approvalSubmitted])

  // show approve flow when: no error on inputs, not approved or pending, or approved in current session
  // never show if price impact is above threshold in non expert mode
  const showApproveFlow =
    approval === ApprovalState.NOT_APPROVED ||
    approval === ApprovalState.PENDING ||
    (approvalSubmitted && approval === ApprovalState.APPROVED)

  const handleExecuteOrder = async () => {
    setOrderLoading(true)
    try {
      const txHash = await executeOrderCallback?.()
      setOrderLoading(false)
    } catch (e) {
      setOrderError(e.message)
      setOrderLoading(false)
    }
  }

  const ExecuteOrderButton = ({ ...props }) => (
    orderLoading || approval === ApprovalState.UNKNOWN
      ? <Spinner animation="border" variant="primary" />
      : <Button
          onClick={() => handleExecuteOrder()}
          id="execute-order-button"
          disabled={!!amount && approval !== ApprovalState.APPROVED}
          variant="primary"
          {...props}
        >
          Execute Order
        </Button>
  )

  if (!order) {
    return <></>
  }

  return (
    <Modal title="Execute order" onDismiss={onDismiss}>
      <Box mb="16px" style={{ color: 'white'}}>
        <CurrencyInputPanel
          label="You Pay"
          value={amount}
          showMaxButton={Number(amount) !== orderMaxAmount}
          currency={order.quoteCurrency}
          onUserInput={value => setAmount(Number(value) <= orderMaxAmount ? value : orderMaxAmount.toString())}
          onMax={() => setAmount(orderMaxAmount.toString())}
          disableCurrencySelect
          otherCurrency={order.baseCurrency}
          id="execute-order"
        />
        <p style={{ margin: '16px 0 32px' }}>
          You receive: {receiveAmount?.toSignificant(order.baseCurrency.decimals ?? 18)} {order.baseCurrency.symbol}
        </p>

        {
          showApproveFlow
            ? <BottomGrouping>
                <RowBetween>
                  <Button
                    onClick={approveCallback}
                    disabled={approval !== ApprovalState.NOT_APPROVED || approvalSubmitted}
                    variant={approval === ApprovalState.APPROVED ? 'success' : 'primary'}
                  >
                    {approval === ApprovalState.PENDING ? (
                      <AutoRow gap="6px" justify="center">
                        Approving <Loader stroke="white" />
                      </AutoRow>
                    ) : approvalSubmitted && approval === ApprovalState.APPROVED ? (
                      'Approved'
                    ) : (
                      `Approve ${order.quoteCurrency.symbol}`
                    )}
                  </Button>
                  <ExecuteOrderButton style={{ width: '48%' }} />
                </RowBetween>
              </BottomGrouping>
          : <ExecuteOrderButton style={{ width: '100%' }} />
        }
        
      </Box>
    </Modal>
  )
}

