import { parseUnits } from '@ethersproject/units'
import { Currency, CurrencyAmount, ETHER, JSBI, Token, TokenAmount, Trade } from '@pancakeswap-libs/sdk'
import { OpenCurrencyAmount, Order } from 'entities/Order'
import { useCallback, useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory, useParams } from 'react-router-dom'
import defaultTokenJson from '../../constants/token/pancakeswap.json'
import { useActiveWeb3React } from '../../hooks'
import { useCurrency } from '../../hooks/Tokens'
import { useTradeExactIn } from '../../hooks/Trades'
import { AppDispatch, AppState } from '../index'
import { useCurrencyBalances } from '../wallet/hooks'
import { Field, replaceSwapState, selectCurrency, setRecipient, switchCurrencies, typeInput } from './actions'
import { SwapState } from './reducer'


export function useSwapState(): AppState['swap'] {
  return useSelector<AppState, AppState['swap']>((state) => state.swap)
}

export function useSwapActionHandlers(): {
  onCurrencySelection: (field: Field, currency: Currency) => void
  onSwitchTokens: () => void
  onUserInput: (field: Field, typedValue: string) => void
  onChangeRecipient: (recipient: string | null) => void
} {
  const dispatch = useDispatch<AppDispatch>()
  const history = useHistory()
  const { pair, chain } = useParams<{ pair: string, chain: string }>()

  const onCurrencySelection = useCallback(
    (field: Field, currency: Currency) => {
      if ([Field.INPUT, Field.OUTPUT].includes(field)) {
        const [input, output] = pair.split('_')
        const tokenUrl = currency === ETHER
          ? 'BNB'
          : (currency as any)?.tokenInfo?.symbol ?? (currency as Token).address
        const newPair = field === Field.INPUT ? `${tokenUrl}_${output}` : `${input}_${tokenUrl}`
        history.push(`/trade/${chain}/${newPair}`)
      }
      dispatch(
        selectCurrency({
          field,
          currencyId: currency instanceof Token ? currency.address : currency === ETHER ? 'BNB' : '',
        })
      )
    },
    [history, chain, pair, dispatch]
  )

  const onSwitchTokens = useCallback(() => {
    const [input, output] = pair.split('_')
    history.push(`/trade/${chain}/${output}_${input}`)
    dispatch(switchCurrencies())
  }, [history, chain, pair, dispatch])

  const onUserInput = useCallback(
    (field: Field, typedValue: string) => {
      dispatch(typeInput({ field, typedValue }))
    },
    [dispatch]
  )

  const onChangeRecipient = useCallback(
    (recipient: string | null) => {
      dispatch(setRecipient({ recipient }))
    },
    [dispatch]
  )

  return {
    onSwitchTokens,
    onCurrencySelection,
    onUserInput,
    onChangeRecipient,
  }
}

// try to parse a user entered amount for a given token
export function tryParseAmount(value?: string, currency?: Currency): CurrencyAmount | undefined {
  if (!value || !currency) {
    return undefined
  }
  try {
    const typedValueParsed = parseUnits(value, currency.decimals).toString()
    if (typedValueParsed !== '0') {
      return currency instanceof Token
        ? new TokenAmount(currency, JSBI.BigInt(typedValueParsed))
        : CurrencyAmount.ether(JSBI.BigInt(typedValueParsed))
    }
  } catch (error) {
    // should fail if the user specifies too many decimal places of precision (or maybe exceed max uint?)
    console.info(`Failed to parse input amount: "${value}"`, error)
  }
  // necessary for all paths to return a value
  return undefined
}

// from the current swap inputs, compute the best trade and return it.
export function useDerivedSwapInfo(): {
  currencies: { [field in Field]?: Currency }
  currencyBalances: { [field in Field]?: CurrencyAmount }
  inputAmount: CurrencyAmount | undefined
  independentAmount: CurrencyAmount | undefined
  order: Order | null
  priceTrade: Trade | null
  inputError?: string
} {
  const { account } = useActiveWeb3React()

  const {
    independentField,
    inputValue,
    independentValue,
    [Field.INPUT]: { currencyId: inputCurrencyId },
    // [Field.PRICE]: { currencyId: priceCurrencyId },
    [Field.OUTPUT]: { currencyId: outputCurrencyId },
  } = useSwapState()
  const inputCurrency = useCurrency(inputCurrencyId)
  const outputCurrency = useCurrency(outputCurrencyId)
  const priceCurrency = outputCurrency // useCurrency(priceCurrencyId)

  const relevantTokenBalances = useCurrencyBalances(account ?? undefined, [
    inputCurrency ?? undefined,
    outputCurrency ?? undefined,
  ])

  // Used to fetch the current price
  const priceTrade = useTradeExactIn(tryParseAmount('1', inputCurrency ?? undefined), outputCurrency ?? undefined)

  const independentCurrency = independentField === Field.PRICE ? priceCurrency : outputCurrency
  const inputAmount = tryParseAmount(inputValue, inputCurrency ?? undefined)
  const independentAmount = tryParseAmount(independentValue, independentCurrency ?? undefined)

  const priceAmount = independentField === Field.PRICE
    ? independentAmount || tryParseAmount(
        priceTrade?.executionPrice.raw.toSignificant(18), independentCurrency ?? undefined
      )
    : independentAmount && new OpenCurrencyAmount(
        independentAmount.currency,
        JSBI.divide(inputAmount?.raw || JSBI.BigInt(0), independentAmount.raw)
      )

  let order: Order | null = null
  if (inputAmount && priceAmount && inputCurrency && outputCurrency) {
    order = {
      amount: inputAmount,
      price: priceAmount,
      baseCurrency: inputCurrency,
      quoteCurrency: outputCurrency,
    }
  }

  const currencyBalances = {
    [Field.INPUT]: relevantTokenBalances[0],
    [Field.OUTPUT]: relevantTokenBalances[1],
  }

  const currencies: { [field in Field]?: Currency } = {
    [Field.INPUT]: inputCurrency ?? undefined,
    [Field.OUTPUT]: outputCurrency ?? undefined,
  }

  let inputError: string | undefined
  if (!account) {
    inputError = 'Connect Wallet'
  }

  if (!currencies[Field.INPUT] || !currencies[Field.OUTPUT]) {
    inputError = inputError ?? 'Select a token'
  }

  if (!inputAmount) {
    inputError = inputError ?? 'Enter an amount'
  }

  if (!independentAmount && !priceTrade) {
    inputError = inputError ?? 'Loading...'
  }

  // compare input balance to max input
  if (inputAmount && currencyBalances[Field.INPUT]?.lessThan(inputAmount)) {
    inputError = `Insufficient ${inputCurrency?.symbol} balance`
  }

  return {
    currencies,
    currencyBalances,
    inputAmount,
    independentAmount,
    order,
    priceTrade,
    inputError,
  }
}

