export function getCurrencyId (currency: string) {
  if (currency.toUpperCase() === 'BNB' || currency.startsWith('0x')) {
    return currency
  }
  const tokenList = defaultTokenJson.tokens
  const token = tokenList.find(item => item.symbol.toUpperCase() === currency.toUpperCase())
  return token?.address
}

export function urlParamsToSwapState(pair: string): SwapState {
  const [inputCurrency, outputCurrency] = pair.split('_')
  const inputCurrencyId = getCurrencyId(inputCurrency)
  const outputCurrencyId = getCurrencyId(outputCurrency)

  return {
    [Field.INPUT]: {
      currencyId: inputCurrencyId,
    },
    [Field.PRICE]: {
      currencyId: inputCurrencyId,
    },
    [Field.OUTPUT]: {
      currencyId: outputCurrencyId,
    },
    inputValue: '', // parseTokenAmountURLParameter(parsedQs.exactAmount),
    independentValue: '', // parseTokenAmountURLParameter(parsedQs.exactAmount),
    independentField: Field.PRICE,
    recipient: null,
  }
}

// updates the swap state to use the defaults for a given network
export function useDefaultsFromURL():
  | { inputCurrencyId: string | undefined; outputCurrencyId: string | undefined }
  | undefined {
  const { chainId } = useActiveWeb3React()
  const dispatch = useDispatch<AppDispatch>()
  const { pair } = useParams<{ pair: string }>()
  const [result, setResult] = useState<
    { inputCurrencyId: string | undefined; outputCurrencyId: string | undefined } | undefined
  >()

  useEffect(() => {
    if (!chainId) return
    const parsed = urlParamsToSwapState(pair)

    dispatch(
      replaceSwapState({
        inputValue: parsed.inputValue,
        independentValue: parsed.independentValue,
        field: parsed.independentField,
        inputCurrencyId: parsed[Field.INPUT].currencyId,
        outputCurrencyId: parsed[Field.OUTPUT].currencyId,
        recipient: parsed.recipient,
      })
    )

    setResult({ inputCurrencyId: parsed[Field.INPUT].currencyId, outputCurrencyId: parsed[Field.OUTPUT].currencyId })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dispatch, chainId])

  return result
}
