import { useWalletModal } from '@pancakeswap-libs/uikit'
import { useWeb3React } from '@web3-react/core'
import useAuth from 'hooks/useAuth'
import React from 'react'
import { Button, Navbar } from 'react-bootstrap'

export default function NavBar () {
  const { account } = useWeb3React()
  const { login, logout } = useAuth()
  const { onPresentConnectModal, onPresentAccountModal } = useWalletModal(login, logout, account as string)
  const accountEllipsis = account ? `${account.substring(0, 4)}...${account.substring(account.length - 4)}` : null

  return (
    <Navbar bg="transparent" variant="dark" style={{ marginBottom: 32, padding: '16px 32px' }}>
      <Navbar.Brand href="/">Orders.finance</Navbar.Brand>
      
    <Navbar.Toggle />
    <Navbar.Collapse className="justify-content-end">
    {account ? (
          <Button variant="info" onClick={onPresentAccountModal}>
            {accountEllipsis}
          </Button>
        ) : (
          <Button onClick={onPresentConnectModal}>
            Connect
          </Button>
        )}
    </Navbar.Collapse>


      {/* <Nav className="mr-auto">
        <Nav.Link href="#home">Home</Nav.Link>
        <Nav.Link href="#features">Features</Nav.Link>
        <Nav.Link href="#pricing">Pricing</Nav.Link>
      </Nav> */}
    </Navbar>
  )
}
