import { Currency } from "@pancakeswap-libs/sdk";
import { OpenOrder } from "entities/Order";
import { Contract } from "ethers";
import { formatUnits } from "ethers/lib/utils";
import { useActiveWeb3React } from "hooks";
import { useEffect, useMemo, useState } from "react";
import { getOrderBookContract } from "utils";
import getCurrencyAddress from "utils/getCurrencyAddress";

function useOrderBookContract (): Contract | null{
  const { account, chainId, library } = useActiveWeb3React()

  return useMemo(() => {
    if (!library || !account || !chainId) {
      return null
    }

    const contract =  getOrderBookContract(chainId, library, account)
    return contract
  }, [library, account, chainId])
}

export default function useOrderList (
  baseCurrency?: Currency,
  quoteCurrency?: Currency
): { loading: boolean, orderList: OpenOrder[], refresh: () => Promise<void>, error: Error | null} {
  const contract = useOrderBookContract()
  const [orderList, setOrderList] = useState<OpenOrder[]>([])
  const [loading, setLoading] = useState(!!baseCurrency && !!quoteCurrency)
  const [error, setError] = useState<Error | null>(null)

  const fetchOrderList = async (orderBook: Contract, base: Currency, quote: Currency) => {
    const baseAddress = getCurrencyAddress(base)
    const quoteAddress = getCurrencyAddress(quote)
    try {
      const list = (await orderBook.callStatic.getPairOrders(baseAddress, quoteAddress))
      .filter(order => order.id && !order.id.eq(0))
      .map(order => ({
        id: order.id,
        price: Number(order.price),
        amount: Number(formatUnits(order.amount, base.decimals || 18)),
        maker: order.maker,
        baseCurrency: base,
        quoteCurrency: quote
      }))
      .sort((a, b) => a.price - b.price)
      setOrderList(list)
      setLoading(false)
    } catch (err) {
      setError(err)
    }
  }

  const refresh = async () => {
    if (contract && baseCurrency && quoteCurrency) {
      await fetchOrderList(contract, baseCurrency, quoteCurrency)
    }
  }

  useEffect(() => {
    if (contract && baseCurrency && quoteCurrency) {
      fetchOrderList(contract, baseCurrency, quoteCurrency)
    }
  }, [contract, baseCurrency, quoteCurrency])

  return {
    orderList,
    loading,
    refresh,
    error
  }
}
